.text-center {
    text-align: center;
}

body {
    background-color: #f5f5f5;
    font-family:'Roboto', sans-serif;
}

b {
    font-weight: 700;
}
.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px 30px;
}

/* CARD */

.card-item {
    background-color: #fff;
    border-radius: 4px;
    max-width: 300px;
    width: 100%;
    padding: 40px 35px;
    box-shadow: 1px 1px 5px #e3e3e3;
    position: relative;
}

.card-item p {
    font-weight: 300;
}

.card-item a {
    color: #000;
}

.card-item .card-link {
    word-break: break-all;
}

.btn-close {
    position: absolute;
    top: 12px;
    right: 12px;
}

.btn-close button {
    background-color: transparent;
    border: 0;
    padding: 10px;
    font-weight: bold;
    cursor: pointer;
}

/* FORM */
.container-box {
    border: 1px solid #e3e3e3;
    padding: 40px 35px;
    max-width: 300px;
    width: 100%;
    box-shadow: 1px 1px 5px #dddddd;
}
.input-text {
    width: 100%;
    margin-bottom: 20px;
}
input {
    width: 100%;
    height: 26px;
    padding-left: 5px;
    margin-top: 10px;
    border-radius: 4px;
    border: 1px solid #666;
}
.post-btn {
    width: 100%;
    padding: 10px 0;
    border-radius: 4px;
    background-color: #4984e7;
    border:0;
    color: #fff;
    font-weight: 700;
    cursor:pointer;
}
.post-btn:hover {
    background-color:#7ea9f1;
}

.post-btn:active {
    background-color: #578eeb;
}